.product_info_page {
	padding: 50px 0;
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;

	background-color: var(--white-color);
}

.product_info_wrapper {
	margin: 0 auto;
	max-width: 1000px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	gap: 60px;
}

.product_title {
	text-align: center;
	font-size: 28px;
}

.product_image_wrapper {
	max-width: 500px;
	height: 400px;
}

.product_image {
	max-width: 100%;
	width: 100%;
	height: 100%;
	object-fit: contain;
}

.product_content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	gap: 15px;
}

p span {
	font-size: 20px;
	font-weight: 700;
}